/******************************************************************
STYLESHEET: CONTENT

This file contains all the base styles for the content area,
like wrappers etc.
******************************************************************/

.bg {
    // background: linear-gradient( to top, rgba($c_3, 0.4), rgba($grey_dark, 0.6) ), url('../img/bg.jpg') top center no-repeat;
    // background-size: cover;
    // background-color: $c_dark;
    background-color: $grey_lighter;
    height: 100%;
    transition: all 0.1s ease-in;
}

.wrapper-inner {
    width: 100%;
    min-width: 1024px;
    max-width: 1600px;
    margin: 0 auto;
    position: relative;
    padding: 5em 1em 0;

    @include large {
        padding: 5em 4em 0;
    }

    &::after {
        content: "";
        display: table;
        clear: both;
    }
}

.column {
    position: relative;
    float: left;
    margin-right: 1.4%;

    &--short {
        width: 24.6%;
    }

    &--wide {
        width: 74%;
    }
}
